/**
 * Custom fields in user profile
 */
angular.module('deskworks.UserProfileFields', [
  'ngResource',
  'deskworks.helper',
])

  .factory('UserProfileFields', function($resource, $q, $filter, deskworksConfig, helper) { 'use strict';
    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/fields'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, deserialize] },
      update: { method: 'PUT', transformRequest: [serialize, angular.toJson] }
    });

    return {
      query: function(centerId, userId) {
        return res.query({ centerId: centerId, userId: userId }).$promise;
      },

      save: function(centerId, userId, fields) {
        return res.update({ centerId: centerId, userId: userId }, fields).$promise;
      },
    };

    function deserialize(fields) {
      angular.forEach(fields, function(field) {
        if(field.type === 'date') field.value = $filter('toDate')(field.value);
      });
      return fields;
    }

    function serialize(fields) {
      angular.forEach(fields, function(field) {
        if(field.type === 'date') field.value = $filter('date')(field.value, 'yyyy-MM-dd');
      });
      return fields;
    }
  });
