/**
 * Printer Code sub-service of userProfileService
 */
angular.module('deskworks.userProfile.service.printerCode', [
	'ngResource',
	'deskworks.helper',
	'deskworks.userProfile.cache'
])

	.factory('upPrinterCodeService', function($resource, deskworksConfig, userProfileCache, helper) {
		'use strict';

		var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/printer-codes/:printerCodeId'), {}, {
			update: {method: 'PUT'}
		});

		return {
			new: function() {
				return {
					id: helper.uid()
				};
			},
			isNew: function(prCode) {
				return prCode && helper.isUid(prCode.id);
			},

			save: function(centerId, userId, prCode) {
				var self = this;

				userProfileCache.removeAll();
				if(helper.isUid(prCode.id)) {
					return res.save({centerId: centerId || 0, userId: userId}, self.unresolve(prCode)).$promise.then(function(result) {
						prCode.id = result.id;
						return result;
					});
				} else {
					return res.update({centerId: centerId || 0, userId: userId, printerCodeId: prCode.id}, self.unresolve(prCode)).$promise;
				}
			},

			delete: function(centerId, userId, prCode) {
				userProfileCache.removeAll();
				return res.delete({centerId: centerId || 0, userId: userId, printerCodeId: prCode.id}).$promise;
			},

			resolve: function(prCode) {
				return prCode;
			},

			unresolve: function(prCode) {
				return {
					code: prCode.code
				};
			}
		};
	});
