/**
 * Options for Reservation Rules / Days in Advance Reservations Permitted
 */
angular.module('deskworks.reservationRules.optsDaysInAdvance', [])
  .factory('optsDaysInAdvance', function($rootScope, $translate, $filter) { 'use strict';
    const opts = [
      { id: 0, _name: 'RESERVATION_RULES.UNLIMITED' },
      { id: 180 },
      { id: 120 },
      { id: 90 },
      { id: 60 },
      { id: 45 },
      { id: 30 },
      { id: 14 },
      { id: 7 },
      { id: 1 },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() {
      opts.forEach(opt => {
        opt.name = opt._name ? $translate.instant(opt._name) : $filter('days')(opt.id);
      });
    }

    return opts;
  });
