angular.module('deskworks.inventory.priceList.service', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer'
])

  .factory('priceListService', function($resource, $q, datesDeserializer, deskworksConfig) { 'use strict';
    var desDates = datesDeserializer(['date']);

    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/price-lists/:priceListId'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, desDates] },
      get: { method: 'GET', isArray: false, transformResponse: [angular.fromJson, desDates] },
      update: { method: 'PUT' }
    });

    var resProduct = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/price-lists/:priceListId/price-list-products/:productRelId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      getPricelists: function(centerId) {
        return res.query({ centerId: centerId }).$promise;
      },

      getPricelist: function(centerId, priceListId) {
        if(priceListId === 'new') {
          var pl = {
            id: 'new',
            name: '',
            visibleForVisitors: true,
            useFor: 'none',
            products: [],
            options: { products: [] }
          };
          return $q.when(pl);
        }
        return res.get({ centerId: centerId, priceListId: priceListId }).$promise;
      },

      createPricelist: function(centerId, data) {
        return res.save({ centerId: centerId }, data).$promise;
      },

      updatePricelist: function(centerId, priceListId, data) {
        return res.update({ centerId: centerId, priceListId: priceListId }, data).$promise;
      },

      deletePricelist: function(centerId, priceListId) {
        return res.delete({ centerId: centerId, priceListId: priceListId }).$promise;
      },

      clonePricelist: function(centerId, priceListId, dstCenterId) {
        return res.save({ centerId: centerId, copy: priceListId, destination: dstCenterId }, {}).$promise;
      },

      save: function(centerId, priceList) { // save (create/update) price list
        return (priceList.id ?
          res.update({ centerId: centerId, priceListId: priceList.id }, priceList).$promise :
          res.save({ centerId: centerId }, priceList).$promise)
          .then(function(result) { priceList.id = result.id; return result; });
      },

      createProduct: function(centerId, priceListId, data) {
        return resProduct.save({ centerId: centerId, priceListId: priceListId }, data).$promise;
      },

      updateProduct: function(centerId, priceListId, productRelId, data) {
        return resProduct.update({ centerId: centerId, priceListId: priceListId, productRelId: productRelId }, data).$promise;
      },

      deleteProduct: function(centerId, priceListId, productRelId) {
        return resProduct.delete({ centerId: centerId, priceListId: priceListId, productRelId: productRelId }).$promise;
      }
    };
  });
