/**
 * Options for 'Count Check-Ins per' in Inventory / Product Edit
 */
angular.module('deskworks.inventory.optsCountCheckInsPer', [])

  .factory('optsCountCheckInsPer', function($rootScope, $translate) {
    const opts = [
      { id: 'coMember', _name: 'PRODUCTS.COUNT_CHECK_INS_PER.CO_MEMBER' },
      { id: 'company', _name: 'PRODUCTS.COUNT_CHECK_INS_PER.COMPANY' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(opt => { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
