/**
 * Options for Members -> Our Members -> Order By
 */
angular.module('deskworks.optsOurMembersSort', [])

  .factory('optsOurMembersSort', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'name', _name: 'OUR_MEMBERS.SORT.NAME' },
      { id: 'occupation', _name: 'OUR_MEMBERS.SORT.OCCUPATION' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
