angular.module('deskworks.memberUsageService', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer'
])

  .factory('memberUsageService', function($resource, $filter, deskworksConfig, helper, datesDeserializer) { 'use strict';
    var desDates = datesDeserializer([
      'usages.date',
      'usages.reservationLog.startTime', 'usages.reservationLog.endTime',
      'usages.checkInLog.startTime', 'usages.checkInLog.endTime'
    ]);

    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/member-usages/:memberUsageId'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, desDates] },
      get: { method: 'GET', isArray: false, transformResponse: [angular.fromJson, desDates] },
      update: { method: 'PUT' }
    });

    var resUsage = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/member-usages/:memberUsageId/usages/:usageId'), {}, {});

    return {
      query: function(centerId, userId, page, pageSize) {
        return res.query({
          centerId: centerId || 0,
          userId: userId,
          page: page || 1,
          pageSize: pageSize || 10
        }).$promise;
      },

      get: function(centerId, userId, memberUsageId, page, pageSize) {
        return res.get({
          centerId: centerId,
          userId: userId,
          memberUsageId: memberUsageId,
          page: page || 1,
          pageSize: pageSize || 10
        }).$promise;
      },

      deletePassRefill: function(centerId, userId, memberUsageId, usageId) {
        return resUsage.delete({
          centerId: centerId,
          userId: userId,
          memberUsageId: memberUsageId,
          usageId: usageId
        }).$promise;
      }
    };
  });
