/**
 * ReservationPrice service
 */
export default angular.module('deskworks.ReservationPrice', [
  'ngResource',
  'deskworks.config',
])
  .factory('ReservationPrice', function($resource, $q, deskworksConfig) {
    const url = deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/reservation-prices');

    const res = $resource(url, { centerId: '@centerId', userId: '@userId' }, { query: { method: 'GET', isArray: true } });

    return {
      query: (centerId, userId) => res.query({ centerId, userId }).$promise,
    };
  });
