/**
 * Options for User Role
 */
angular.module('deskworks.userRoles', [])

  .factory('userRoles', function($rootScope, $translate) { 'use strict';
    var roles = {
      nonMember: { _name: 'GLOBAL.ROLE.NON_MEMBER', color: 'default', type: 'user' },
      member: { _name: 'GLOBAL.ROLE.MEMBER', color: 'primary', type: 'user' },
      accountant: { _name: 'GLOBAL.ROLE.ACCOUNTANT', color: 'info', type: 'admin' },
      coordinator: { _name: 'GLOBAL.ROLE.COORDINATOR', color: 'info', type: 'admin' },
      admin: { _name: 'GLOBAL.ROLE.ADMIN', color: 'info', type: 'admin' },
      management: { _name: 'GLOBAL.ROLE.MANAGEMENT', color: 'info', type: 'admin' },
      master: { _name: 'GLOBAL.ROLE.MASTER', color: 'info', type: 'admin' },
    };

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() {
      Object.keys(roles).forEach(function(key) { roles[key].name = $translate.instant(roles[key]._name); });
    }

    return roles;
  });
