export default angular.module('deskworks.center.service', [
  'ngResource',
  'deskworks.config',
  'autoCacheFactory',
  'ngFileUpload',
  'deskworks.helper',
  'deskworks.userProfile.optsCountry',
  'deskworks.userProfile.optsState',
])

  .factory('centerService', function($resource, $filter, Upload, deskworksConfig, autoCacheFactory,
                                     helper, optsCountry, optsState) {
    const cache = autoCacheFactory('center', 60);

    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId'), {}, {
      get:    { method: 'GET', cache: cache },
      query:  { method: 'GET', cache: cache, isArray: true },
      update: { method: 'PUT' }
    });

    return {
      resetCache: () => {
        cache.removeAll();
      },
      getCenter: centerId => res.get({ centerId }).$promise,
      getCenters: _ => res.query().$promise,
      createCenter: (data) => {
        cache.removeAll();
        return res.save({}, data).$promise;
      },
      updateCenter: (centerId, data) => {
        cache.removeAll();
        return res.update({ centerId }, data).$promise;
      },
      deleteCenter: (centerId) => {
        cache.removeAll();
        return res.delete({ centerId }).$promise;
      },
      uploadThumbnail: (centerId, file) => {
        cache.removeAll();
        return Upload.upload({
          url: deskworksConfig.getApiAbsUrl('/centers/' + centerId),
          method: 'PUT',
          withCredentials: true,
          data: { photo: file }
        });
      },
      /**
       * Delete photo
       * @param params.centerId
       * @returns {*}
       */
      deletePhoto: params => (cache.removeAll(), res.update(params, { photo: null }).$promise),

      /**
       * New Methods, the above ones are obsolete
       */
      query: () => res.query().$promise,

      get: function(centerId) {
        const self = this;
        return res.get({ centerId }).$promise
          .then(result => self.resolve(result));
      },

      save: function(center) {
        const self = this;
        cache.removeAll();
        if (center.id === 'new') {
          return res.save({}, self.unresolve(center)).$promise
            .then(result => (center.id = result.id, result));
        } else {
          return res.update({ centerId: center.id || 0 }, self.unresolve(center)).$promise;
        }
      },

      /**
       * Convert center data to angular-friendly object
       */
      resolve: function(center) {
        if (!center.id || center.id === 'new') {
          center.id = 'new';
          center.country = helper.findByKeyValue(optsCountry, 'id', 'US');
          center.state = helper.findByKeyValue(optsState.US, 'id', 'CA');
          center.bankId = center.options.banks[0] && center.options.banks[0].id;
          center.taxRates = [];
          center.ownerId = null;
          center.regionId = null;
        } else {
          center.street = center.address; // duplicate data as we have different format in some API calls
          center.country = helper.findByKeyValue(optsCountry, 'id', center.countryId);
          if (center.country && optsState[center.country.id]) {
            center.state = helper.findByKeyValue(optsState[center.country.id], 'id', center.state);
          }
        }

        return center;
      },

      /**
       * Convert angular-friendly center object to POST/PUT-friendly data
       */
      unresolve: function(center) {
        const data = {
          name: center.name,
          shortName: center.shortName,
          street1: center.street1,
          street2: center.street2,
          city: center.city,
          state: center.state.id ? center.state.id : center.state,
          countryId: center.country.id,
          zip: center.zip,
          phone: center.phone,
          fax: center.fax,
          email: center.email,
          bankId: center.bankId,
          forInternalUse: !!center.forInternalUse,
          hideInMemberSignUp: !!center.hideInMemberSignUp,
          description: center.description,
          map: center.map,
          directions: center.directions,
          color: center.color,
          timezone: center.timezone,
          ownerId: center.ownerId,
          regionId: center.regionId,
          taxId: center.taxId,
          taxRates: center.taxRates.map(function(taxRate) { taxRate.rate = +taxRate.rate; return taxRate; })
        };

        if (center.options.qboClasses) {
          data.qboClassId = center.qboClassId;
        }

        return data;
      }
    };
  });
