/**
 * Options for Setup -> Settings -> Free Access Length
 */
export default angular.module('deskworks.setup.optsFreeAccessLength', [])

  .factory('optsFreeAccessLength', function($rootScope, $translate) {
    var opts = [
      { id: 0, _name: 'CENTERS.FREE_ACCESS.DISABLED' },
      { id: 30, hours: 0.5 },
      { id: 60, hours: 1 },
      { id: 90, hours: 1.5 },
      { id: 120, hours: 2 },
      { id: 180, hours: 3 }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() {
      opts.forEach(function(opt) {
        if (opt._name) {
          opt.name = $translate.instant(opt._name);
        } else {
          opt.name = $translate.instant('GLOBAL.HOURS_FILTER', { hoursCount: opt.hours }, 'messageformat');
        }
      });
    }

    return opts;
  });
