/**
 * Task service
 */
angular.module('deskworks.Task', [
  'ngResource',
  'deskworks.config',
  'autoCacheFactory',
])

  .factory('Task', function($resource, deskworksConfig, helper, autoCacheFactory) { 'use strict';
    var res = $resource(deskworksConfig.getApiAbsUrl('/tasks/:taskId'), {}, {
      query: { method: 'GET', isArray: false },
      update: { method: 'PUT' }
    });

    return {
      query: function() {
        return res.query().$promise;
      },
      get: function(taskId) {
        return res.get({ taskId: taskId }).$promise;
      },
      save: function(task) {
        var data = helper.purify(task, ['options']);
        autoCacheFactory.emptyAll();
        if(task.id && task.id !== 'new') return res.update({ taskId: task.id }, data).$promise;
        return res.save({}, data).$promise
          .then(function(result) { task.id = result.id; return task; });
      },
      delete: function(taskId) {
        autoCacheFactory.emptyAll();
        return res.delete({ taskId: taskId }).$promise;
      }
    };
  });
