angular.module('deskworks.bank.service', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer'
])

  .factory('bankService', function($resource, $q, $filter, deskworksConfig, helper, datesDeserializer) { 'use strict';
    var desDates = datesDeserializer(['creditCardCutOffTime']);

    var res = $resource(deskworksConfig.getApiAbsUrl('/banks/:bankId'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: desDates },
      get: { method: 'GET', transformResponse: desDates },
      update: { method: 'PUT' }
    });

    return {
      query: function() {
        return res.query().$promise;
      },

      get: function(bankId) {
        if(bankId === 'new') return $q.when({ id: bankId });
        return res.get({ bankId: bankId }).$promise;
      },

      save: function(bank) {
        var data = angular.copy(bank);
        data.creditCardCutOffTime = $filter('date')(bank.creditCardCutOffTime, 'HH:mm');

        return bank.id && bank.id !== 'new' ?
          res.update({ bankId: bank.id }, data).$promise :
          res.save({}, data).$promise
            .then(function(result) { bank.id = result.id; return result; });
      },

      delete: function(bankId) {
        return res.delete({ bankId: bankId }).$promise;
      },
    };
  });
