export default angular.module('deskworks.OurMembers', [
  'ngResource',
  'deskworks.config'
])

  .factory('OurMembers', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/our-members'));

    return {
      query: params => {
        const p = {
          centerId: params.centerId || null,
          sort: params.sort || null,
          q: params.q || null,
          limit: params.limit || null,
          offset: params.offset || null,
        };
        return res.query(p).$promise;
      },
    };
  });
