/**
 * Options for System Email Function
 */
angular.module('deskworks.optsSystemEmailFunction', [])

  .factory('optsSystemEmailFunction', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 0, _name: 'SYSTEM_EMAILS.FUNCTION.FROM' },
      { id: 1, _name: 'SYSTEM_EMAILS.FUNCTION.SIGN_UP' },
      { id: 2, _name: 'SYSTEM_EMAILS.FUNCTION.LOST_ACCESS_CARD' },
      { id: 3, _name: 'SYSTEM_EMAILS.FUNCTION.NEW_ORDER' },
      { id: 4, _name: 'SYSTEM_EMAILS.FUNCTION.EXISTING_ORDER' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
