/**
 * Roles service
 */
angular.module('deskworks.Roles', [
  'ngResource',
  'deskworks.config',
])

  .factory('Roles', function($resource, deskworksConfig) { 'use strict';
    const res = $resource(deskworksConfig.getApiAbsUrl('/roles'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, assignTranslations] },
    });

    const roleNames = {
      nonMember: 'GLOBAL.ROLE.NON_MEMBER',
      member: 'GLOBAL.ROLE.MEMBER',
      coMember: 'GLOBAL.CO_MEMBER',
      accountant: 'GLOBAL.ROLE.ACCOUNTANT',
      coordinator: 'GLOBAL.ROLE.COORDINATOR',
      admin: 'GLOBAL.ROLE.ADMIN',
      management: 'GLOBAL.ROLE.MANAGEMENT',
      master: 'GLOBAL.ROLE.MASTER',
    };

    return {
      query: () => res.query().$promise,
    };

    function assignTranslations(roles) {
      if (!roles) return;
      roles.forEach(role => { role._name = roleNames[role.id]; });
      return roles;
    }
  });
