angular.module('deskworks.config', [])

  .provider('deskworksConfig', function() { 'use strict';
    var apiUrl = '/api/v1', apiPostfix = '';
    var defaultRoute = '/dashboard';
    var wsUrl = null;
    var siteId = null;
    var sitesUrl = 'https://sites.satellitedeskworks.com/';

    return {
      setApiUrl: function(url) {
        apiUrl = url;
      },
      setApiPostfix: function(postfix) {
        apiPostfix = postfix;
      },
      getDefaultRoute: function() {
        return defaultRoute;
      },
      setDefaultRoute: function(route) {
        defaultRoute = route;
      },
      setWsUrl: function(url) {
        wsUrl = url;
      },
      setSiteId: function(id) {
        siteId = id;
      },
      setSitesUrl: function(url) {
        sitesUrl = url;
      },
      $get: function($window) {
        return {
          getApiDomain: function(customApiUrl) {
            var match = (customApiUrl || apiUrl).match(/^(.*:)\/\/([A-Za-z0-9\-\.]+)(:[0-9]+)?(.*)$/i);
            if (match && match[2]) return match[2];
            return $window.location.hostname;
          },
          getApiOrigin: function() {
            const match = apiUrl.match(/^(.*:)\/\/([A-Za-z0-9\-\.]+)(:[0-9]+)?(.*)$/i);
            if (match && match[2]) return `${match[1]}//${match[2]}${match[3] || ''}`;
            return $window.location.origin;
          },
          getSiteId: function() {
            if (siteId) return siteId;
            var domain = this.getApiDomain(_API_URL_).split('.');
            domain = domain[domain.length - 3];
            if (!domain) throw new Error('configuration issue: siteId is undefined');
            return domain;
          },
          getApiAbsUrl: function(relativeUrl) {
            return apiUrl + relativeUrl + apiPostfix;
          },
          getDefaultRoute: function() {
            return defaultRoute;
          },
          getWsUrl: function() {
            return wsUrl;
          },
          getSitesUrl: function() {
            return sitesUrl;
          }
        };
      }
    };
  });
