/**
 * Options for Setup -> Settings -> Min Pass Fraction Unit
 */
angular.module('deskworks.setup.optsMinPassFractionUnit', [])

  .factory('optsMinPassFractionUnit', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: true, _name: 'SETUP_SETTINGS.MIN_PASS_UNIT.PASSES' },
      { id: false, _name: 'SETUP_SETTINGS.MIN_PASS_UNIT.HOURS' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
