angular.module('deskworks.inventory.membershipCategory.service', [
  'ngResource',
  'deskworks.config'
])

  .factory('membershipCategoryService', function($resource, $q, deskworksConfig) { 'use strict';

    var res = $resource(deskworksConfig.getApiAbsUrl('/membership-types/:membershipTypeId'), {}, {
      update: {method: 'PUT'}
    });

    return {

      query: function() {
        return res.query().$promise;
      },

      get: function(membershipTypeId) {
        return res.get({ membershipTypeId: membershipTypeId }).$promise;
      },

      create: function(data) {
        return res.save({}, data).$promise;
      },

      update: function(membershipTypeId, data) {
        return res.update({ membershipTypeId: membershipTypeId }, data).$promise;
      },

      delete: function(membershipTypeId) {
        return res.delete({ membershipTypeId: membershipTypeId }).$promise;
      },

      save: function(membershipCategory) {
        return (membershipCategory.id ?
          res.update({ membershipTypeId: membershipCategory.id }, membershipCategory).$promise :
          res.save({}, membershipCategory).$promise)
          .then(function(result) { membershipCategory.id = result.id; return result; });
      }
    };
  });
