angular.module('deskworks.reservationService', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer',
])

  .factory('reservationService', function($resource, $filter, deskworksConfig, helper, datesDeserializer) { 'use strict';
    const fDate = $filter('date');
    const desDates = datesDeserializer(['from', 'to', 'updatedAt']);

    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/reservations/:reservationId'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, desDates] },
      save: { method: 'POST', transformRequest: serializer },
      update: { method: 'PUT', transformRequest: serializer },
    });

    const resPayment = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/reservations-with-payments'), {}, {
      save: { method: 'POST', transformRequest: serializer },
    });

    return {
      query: function(params) {
        params = angular.extend({}, params);
        params.from = params.from && fDate(params.from, 'iso');
        params.to = params.to && fDate(params.to, 'iso');
        return res.query(params).$promise;
      },

      save: function(centerId, userId, reservation) {
        if(!reservation.id) return res.save({ centerId: centerId, userId: userId }, reservation).$promise
          .then(function(result) { reservation.id = result.id; reservation.accessCode = result.accessCode; return reservation; });

        return res.update({ centerId: centerId, userId: userId, reservationId: reservation.id }, reservation).$promise;
      },

      saveWithPayment: function(centerId, userId, reservation) {
        return resPayment.save({ centerId, userId }, reservation).$promise;
      },

      delete: function(centerId, userId, reservationId, emailConfirmation, repeatFrequency, repeatDuration) {
        return res.delete({ centerId, userId, reservationId, emailConfirmation, repeatFrequency, repeatDuration }).$promise;
      }
    };

    function serializer(data) {
      data = angular.extend({}, data);
      data.from = fDate(data.from, 'iso');
      data.to = fDate(data.to, 'iso');
      return angular.toJson(data);
    }
  });
