export default angular.module('deskworks.Admin', [
  'ngResource',
  'deskworks.config',
  'deskworks.helper',
])

  .factory('Admin', function($resource, $q, deskworksConfig, helper) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/administrators/:adminId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: () => res.query().$promise,

      get: adminId => res.get({ adminId: adminId }).$promise
        .then(result => {
          if (adminId === 'new') {
            result.role = result.options.roles[0];
            result.receiveUrgentReservationNotices = true;
            result.receiveWelcomeEmailCopies = false;
            result.receiveReservationConfirmationCopies = false;
            result.seeDashboardRevenue = true;
            result.centers.forEach(function(center) { center.emailing = true; center.permission = 'all'; });
          } else {
            result.role = helper.findByKeyValue(result.options.roles, 'id', result.role);
          }
          result.centers = result.centers || {};
          result.fullName = result.fullName || helper.join([result.firstName, result.lastName], ' ');
          return result;
        }),

      save: admin => {
        const data = helper.purify(admin, ['options', 'printerCode']);
        data.role = data.role && data.role.id ? data.role.id : data.role;
        if (admin.id && admin.id !== 'new') return res.update({ adminId: admin.id }, data).$promise;
        return res.save({}, data).$promise
          .then(result => {
            admin.id = result.id;
            return admin;
          });
      },

      delete: adminId => res.delete({ adminId: adminId }).$promise,
    };
  });
