/**
 * ReservationRules resource
 */
angular.module('deskworks.reservationRules.service', [
  'ngResource',
  'deskworks.config',
])

  .factory('ReservationRules', function($resource, $timeout, $filter, $rootScope, $translate, $locale, helper, deskworksConfig) { 'use strict';
    const urlCenter = deskworksConfig.getApiAbsUrl('/centers/:centerId/reservation-rules/:ruleId');
    const urlUnit = deskworksConfig.getApiAbsUrl('/centers/:centerId/reservation-units/:reservationUnitId/reservation-rules/:ruleId');

    const resCenter = $resource(urlCenter, { centerId: '@centerId', ruleId: '@ruleId' }, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, deserialize] },
      save: { method: 'POST', transformRequest: [serialize, angular.toJson], transformResponse: [angular.fromJson, deserialize] },
      update: { method: 'PUT', transformRequest: [serialize, angular.toJson], transformResponse: [angular.fromJson, deserialize] },
      delete: { method: 'DELETE' },
    });

    const resUnit = $resource(urlUnit, { centerId: '@centerId', reservationUnitId: '@reservationUnitId', ruleId: '@ruleId' }, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, deserialize] },
      save: { method: 'POST', transformRequest: [serialize, angular.toJson], transformResponse: [angular.fromJson, deserialize] },
      update: { method: 'PUT', transformRequest: [serialize, angular.toJson], transformResponse: [angular.fromJson, deserialize] },
    });

    const roles = [
      { id: 'member', _name: 'RESERVATION_RULES.MEMBER', $isVisible: whoCanReserve => whoCanReserve !== 'staffOnly' && whoCanReserve !== 'notAvailable' && whoCanReserve !== 'nonMember' },
      { id: 'nonMember', _name: 'RESERVATION_RULES.NON_MEMBER', $isVisible: whoCanReserve => whoCanReserve === 'anyone' || whoCanReserve === 'nonMember' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { roles.forEach(opt => { opt.name = $translate.instant(opt._name); }); }

    return {
      roles: roles,

      /**
       * Query
       */
      query: params => (params.reservationUnitId ? resUnit.query(params).$promise : resCenter.query(params).$promise),

      /**
       * Save / Update
       */
      save: (params, rule) => {
        params = Object.assign({ ruleId: rule.id }, params);
        let promise;
        if (rule.id) {
          promise = params.reservationUnitId ? resUnit.update(params, rule).$promise : resCenter.update(params, rule).$promise;
        } else {
          promise =  params.reservationUnitId ? resUnit.save(params, rule).$promise : resCenter.save(params, rule).$promise;
        }
        return promise.then(result => { // update id and intervals
          rule.id = result.id;
          roles.forEach(role => {
            if (result[role.id] && result[role.id].intervals) {
              rule[role.id].intervals = result[role.id].intervals;
            }
          });
          return rule;
        });
      },

      /**
       * Delete Annual Holiday Calendar rule
       */
      delete: params => resCenter.delete(params).$promise,
    };

    /**
     * Deserialize rules
     * @param rule
     */
    function deserialize(rule) {
      const rules = angular.isArray(rule) ? rule : [rule];
      rules.forEach(rule => {
        switch(rule.uid) {
          case 'whoCanReserve': rule.type = 'value'; break;
          case 'scale': rule.type = 'value'; break;
          case 'buffer': rule.type = 'value'; break;
          case 'fullTimeProduct': rule.type = 'value'; break;
          case 'attachedUser': rule.type = 'static'; break;
          case 'limitMin':
          case 'limitMax':
          case 'daysInAdvance': rule.type = 'roles'; break;
          case 'timeInAdvance': rule.type = 'roles'; break;
          case 'nestedUnits': rule.type = 'checklist'; break;
          case 'sun': rule.name = $locale.DATETIME_FORMATS.SHORTDAY[0]; rule.type = 'weekday'; break;
          case 'mon': rule.name = $locale.DATETIME_FORMATS.SHORTDAY[1]; rule.type = 'weekday'; break;
          case 'tue': rule.name = $locale.DATETIME_FORMATS.SHORTDAY[2]; rule.type = 'weekday'; break;
          case 'wed': rule.name = $locale.DATETIME_FORMATS.SHORTDAY[3]; rule.type = 'weekday'; break;
          case 'thu': rule.name = $locale.DATETIME_FORMATS.SHORTDAY[4]; rule.type = 'weekday'; break;
          case 'fri': rule.name = $locale.DATETIME_FORMATS.SHORTDAY[5]; rule.type = 'weekday'; break;
          case 'sat': rule.name = $locale.DATETIME_FORMATS.SHORTDAY[6]; rule.type = 'weekday'; break;
          default: rule.type = 'date';
        }

        if (rule.type === 'weekday' || rule.type === 'date') {
          roles.forEach(role => {
            rule[role.id].intervals.forEach(interval => {
              interval.from = $filter('toDate')(interval.from);
              interval.to = $filter('toDate')(interval.to);
            });
          });
        }

        if (rule.type === 'date') {
          rule.date = $filter('toDate')(rule.date);
        }

        if (rule.type === 'checklist') {
          rule.value = rule.value && rule.value.reduce((o, i) => (o[i] = true, o), {}); // array to object
        }
      });

      return rule;
    }

    /**
     * Serialize rule for POST/PUT
     * @param rule - rule to serialize
     * @returns {*} - rule passed
     */
    function serialize(rule) {
      const type = rule.type;
      rule = angular.copy(helper.purify(rule, ['type']));

      if (type === 'weekday' || type === 'date') {
        roles.forEach(role => {
          rule[role.id] = helper.purify(rule[role.id]);
          angular.forEach(rule[role.id].intervals, interval => {
            interval.from = $filter('date')(interval.from, 'HH:mm');
            interval.to = $filter('date')(interval.to, 'HH:mm');
            if (interval.to === '00:00') interval.to = '24:00';
          });
        });
      }

      if (type === 'date') {
        rule.date = $filter('date')(rule.date, 'yyyy-MM-dd');
      }

      if (type === 'checklist') {
        rule.value = rule.value && Object.keys(rule.value).filter(key => !!rule.value[key]).map(key => +key);
      }

      return rule;
    }
  });
