export default angular.module('deskworks.SplashPage', [
  'ngResource',
  'deskworks.config'
])

  .factory('SplashPage', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/net-check-in/splash-page/:centerId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      get: function(centerId) {
        return res.get({ centerId: centerId }).$promise;
      },

      save: function(centerId, data) {
        return res.update({ centerId: centerId }, data).$promise;
      },
    };
  });
