/**
 * Options for Mailbox Handling drop-down in User Profile
 */
angular.module('deskworks.userProfile.optsMailboxHandling', [])

  .factory('optsMailboxHandling', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'dropOff', _name: 'USER_PROFILE.MAILBOXES.HANDLING.DROP_OFF' },
      { id: 'mailbox', _name: 'USER_PROFILE.MAILBOXES.HANDLING.MAILBOX' },
      { id: 'forward', _name: 'USER_PROFILE.MAILBOXES.HANDLING.FORWARD' },
      { id: 'email', _name: 'USER_PROFILE.MAILBOXES.HANDLING.EMAIL' },
      { id: 'spheremail', _name: 'USER_PROFILE.MAILBOXES.HANDLING.SPHEREMAIL' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
