angular.module('deskworks.reservationCategory.service', [
  'ngResource',
  'deskworks.config',
  'ngFileUpload'
])
  .factory('reservationCategoryService', function($resource, $q, Upload, deskworksConfig) { 'use strict';

    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/reservation-types/:reservationTypeId/:unavailableDates'), {}, {
      update: {method: 'PUT'}
    });

    var resPhoto = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/reservation-types/:reservationTypeId/photos/:photoId'), {}, {
      update: {method: 'PUT'}
    });

    return {
      get: function(centerId, reservationTypeId) {
        if(reservationTypeId === 'new') {
          return $q.when({ id: 'new', cancellationNonMemberNoticeHours: 24, cancellationMemberNoticeHours: 24 });
        }
        return res.get({ centerId: centerId, reservationTypeId: reservationTypeId }).$promise
          .then(cat => {
            if (cat.photos[0] && cat.photos[0].id === null) {
              cat.photos.splice(0, 1);
            }
            return cat;
          });
      },
      query: function(centerId) {
        return res.query({ centerId: centerId }).$promise;
      },
      create: function(centerId, data) {
        return res.save({ centerId: centerId }, data).$promise;
      },
      update: function(centerId, reservationTypeId, data) {
        return res.update({ centerId: centerId, reservationTypeId: reservationTypeId }, data).$promise;
      },
      delete: function(centerId, reservationTypeId) {
        return res.delete({ centerId: centerId, reservationTypeId: reservationTypeId }).$promise;
      },

      save: function(centerId, resCategory) {
        return resCategory.id ?
          res.update({ centerId: centerId, reservationTypeId: resCategory.id }, resCategory).$promise :
          res.save({ centerId: centerId }, resCategory).$promise
            .then(function(result) { resCategory.id = result.id; return result; });
      },

      getUnavailableDates: function(centerId, reservationTypeId) {
        return res.query({ centerId: centerId, reservationTypeId: reservationTypeId, unavailableDates: 'unavailable-dates' }).$promise;
      },

      /**
       * Photos
       */
      createPhoto: function(centerId, reservationTypeId, file) {
        return Upload.upload({
          url: deskworksConfig.getApiAbsUrl('/centers/' + centerId + '/reservation-types/' + reservationTypeId + '/photos'),
          method: 'POST',
          withCredentials: true,
          data: { photo: file }
        });
      },
      updatePhoto: function(centerId, reservationTypeId, photoId, file) {
        return Upload.upload({
          url: deskworksConfig.getApiAbsUrl('/centers/' + centerId + '/reservation-types/' + reservationTypeId + '/photos/' + photoId),
          method: 'PUT',
          withCredentials: true,
          data: { photo: file }
        });
      },
      deletePhoto: function(centerId, reservationTypeId, photoId) {
        return resPhoto.delete({ centerId: centerId, reservationTypeId: reservationTypeId, photoId: photoId }).$promise;
      }
    };
  });
