/**
 * Options for Product Type
 */
angular.module('deskworks.inventory.optsProductType', [])

  .factory('optsProductType', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'membership', _name: 'PRODUCTS.PRODUCT_TYPE.MEMBERSHIP', import: 'Membership' },
      { id: 'registration', _name: 'PRODUCTS.PRODUCT_TYPE.REGISTRATION', import: 'Registration' },
      { id: 'rental', _name: 'PRODUCTS.PRODUCT_TYPE.RENTAL_HOURS', import: 'Rental Hours' },
      { id: 'print', _name: 'PRODUCTS.PRODUCT_TYPE.PRINT', import: 'Print' },
      { id: 'services', _name: 'PRODUCTS.PRODUCT_TYPE.SERVICES', import: 'Services' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
