angular.module('deskworks.MessageBoard', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer',
])

  .factory('MessageBoard', function($resource, $timeout, deskworksConfig, datesDeserializer, helper) { 'use strict';
    var desDates = datesDeserializer(['createdAt']);

    // var res = $resource('http://0.0.0.0:3015' + '/api/messages/:messageId', {}, {
    var res = $resource(deskworksConfig.getApiAbsUrl('/message-board/messages/:messageId'), {}, {
      query: { method: 'GET', isArray: true, transformResponse: [angular.fromJson, deserialize] },
      update: { method: 'PUT' },
    });

    return {
      query: function(params) {
        return res.query(params).$promise;
      },

      save: function(data) {
        if(data.id) return res.update({ messageId: data.id }, data).$promise;

        return res.save({}, data).$promise
          .then(function(result) { data.id = result.id; return result; });
      },

      delete: function(messageId) {
        return res.delete({ messageId: messageId }).$promise;
      },

      deserialize: deserialize,
    };

    function deserialize(data) {
      data = desDates(data);

      angular.forEach(angular.isArray(data) ? data : [data], function(msg) {
        if(!msg || !msg.author) return;
        msg.author.fullName = helper.join([msg.author.firstName, msg.author.lastName], ' ');
      });

      return data;
    }
  });
