/**
 * Options for User Profile -> Shareable Info -> Email
 */
angular.module('deskworks.optsShareEmail', [])

  .factory('optsShareEmail', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'private', _name: 'USER_PROFILE.SHAREABLE_INFO.EMAIL.PRIVATE' },
      { id: 'members', _name: 'USER_PROFILE.SHAREABLE_INFO.EMAIL.MEMBERS' },
      { id: 'public', _name: 'USER_PROFILE.SHAREABLE_INFO.EMAIL.PUBLIC' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
