/**
 * Options for Reservation Rules / Who Can Reserve
 */
angular.module('deskworks.reservationRules.optsWhoCanReserve', [])
  .factory('optsWhoCanReserve', function($rootScope, $translate) { 'use strict';
    const opts = [
      { id: 'anyone', _name: 'RESERVATION_RULES.WHO_CAN_RESERVE.ANYONE' },
      { id: 'nonMember', _name: 'RESERVATION_RULES.WHO_CAN_RESERVE.NON_MEMBER' },
      { id: 'member', _name: 'RESERVATION_RULES.WHO_CAN_RESERVE.MEMBER' },
      { id: 'staffOnly', _name: 'RESERVATION_RULES.WHO_CAN_RESERVE.STAFF_ONLY' },
      { id: 'notAvailable', _name: 'RESERVATION_RULES.WHO_CAN_RESERVE.NOT_AVAILABLE' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(opt => { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
