/**
 * Photo sub-service of userProfileService
 */
export default angular.module('deskworks.userProfile.service.photo', [
  'ngResource',
  'ngFileUpload',
  'deskworks.config',
  'deskworks.helper',
  'deskworks.userProfile.cache',
])

  .factory('upPhotoService', function($resource, $q, deskworksConfig, userProfileCache, helper, Upload) {

    return {
      isNew: user => !!user && !!user.photoFile,

      save: function (centerId, user, file) {
        const self = this;

        if (self.isNew(user)) {
          userProfileCache.removeAll();
          return Upload.upload({
            url: deskworksConfig.getApiAbsUrl(`/centers/${centerId || 0}/` + ((user && user.membershipStatus && (user.membershipStatus.id === 'active' || user.membershipStatus.id === 'inactive')) ? 'members' : 'non-members') + '/' + user.id),
            method: 'PUT',
            withCredentials: true,
            data: { photo: user.photoFile },
          })
            .then(result => {
              user.photo = result.data.photo;
              delete user.photoFile;
              return result.data;
            });
        }
        return $q.when();
      },

      /**
       * @param params.user
       * @returns {Resource.$promise}
       */
      delete: params => {
        const { user } = params;
        const centerId = 0;
        // const res = $resource(deskworksConfig.getApiAbsUrl('/centers/0/user-profiles/:userId'), {}, { update: { method: 'PUT' } });
        const url = deskworksConfig.getApiAbsUrl(`/centers/${centerId || 0}/` + ((user && user.membershipStatus && (user.membershipStatus.id === 'active' || user.membershipStatus.id === 'inactive')) ? 'members' : 'non-members') + '/' + user.id);
        const res = $resource(url, {}, { update: { method: 'PUT' } });
        return res.update({}, { photo: null }).$promise
      },
    };
  });
