/**
 * ReservationCost service
 */
export default angular.module('deskworks.ReservationCost', [
  'deskworks.ReservationPrice',
  'deskworks.calendarService',
])
  .factory('ReservationCost', function($q, $filter, dwAlerts, ReservationPrice, calendarService) {

    return {
      /**
       * Sync calculation - used in Reservation Form for estimated reservation cost
       */
      calcSync: (timeStart, timeEnd, reservationPrices, resCategoryId) => {
        if (!reservationPrices) return null;

        const resCategory = reservationPrices.find(rp => rp.id === resCategoryId);
        const length = (timeEnd - timeStart) / 60000;

        if (!resCategory) return null;

        return resCategory.passes
          .map(pass => minutes_to_passes(length, +pass.passSize, +pass.passMin, +pass.passMax) * pass.passPrice)
          .reduce((prev, curr) => curr < prev ? curr : prev);
      },

      /**
       * Async calculation - used for skipping purchase for free reservations
       * in Simple Reservation wizard after User Profile step
       */
      calcAsync: (centerId, userId, reservationUnitId, timeStart, timeEnd) => {
        timeStart = $filter('toDate')(timeStart);
        timeEnd = $filter('toDate')(timeEnd);

        return $q.all({
          prices: ReservationPrice.query(centerId, userId)
            .catch(err => dwAlerts.error(err, 'Failed to query reservation prices.')),
          cats: calendarService.query(centerId, timeStart, timeEnd)
            .catch(err => dwAlerts.error(err, 'Failed to query calendar data.')),
        })
          .then(result => {
            const cat = result.cats.find(cat => !!cat.reservationUnits.find(unit => unit.id === +reservationUnitId));
            return cat && result.prices.find(p => p.id === cat.id);
          })
          .then(pricing => {
            if (!pricing) return null;
            const length = (timeEnd - timeStart) / 60000;
            return pricing.passes
              .map(pass => minutes_to_passes(length, +pass.passSize, +pass.passMin, +pass.passMax) * pass.passPrice)
              .reduce((prev, curr) => curr < prev ? curr : prev);
          });
      },
    };

    /**
     * Reservation length to passes conversion logic (from ruby code)
     */
    function minutes_to_passes(minutes, pass_size, pass_min, pass_max) {
      const hrs = round_time_diff(Math.floor(minutes / 60), minutes % 60, pass_size * pass_min);
      let passes = Math.max(+(hrs / (pass_size || 1)).toFixed(2), 1.0);
      if (pass_max > 0 && passes > pass_max) {
        passes = pass_max;
      }
      return passes;
    }
    function round_minutes_diff(minutes, step) {
      const minutes_in_step = 60 * step;
      let parts = Math.floor(minutes / minutes_in_step);
      const fraction_of_part = minutes - parts * minutes_in_step;
      if (fraction_of_part > 5) {
        parts += 1;
      }
      return parts * minutes_in_step / 60.0;
    }
    function round_time_diff(hrs, minutes, step) {
      const total_minutes = hrs * 60 + minutes;
      return round_minutes_diff(total_minutes, step || 0.25);
    }

  });
