/**
 * Options for Membership Status in User Profile
 */
angular.module('deskworks.userProfile.optsMembershipStatus', [])

  .factory('optsMembershipStatus', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'active', _name: 'USER_PROFILE.MEMBERSHIP.STATUS.ACTIVE', import: 'Active' },
      { id: 'inactive', _name: 'USER_PROFILE.MEMBERSHIP.STATUS.INACTIVE', import: 'Inactive' },
      { id: 'nonMember', _name: 'USER_PROFILE.MEMBERSHIP.STATUS.NON_MEMBER', import: 'Non-Member' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
