/**
 * Document sub-service of userProfileService
 */
angular.module('deskworks.userProfile.service.document', [
	'session',
	'ngResource',
	'ngFileUpload',
	'deskworks.helper',
	'deskworks.userProfile.cache'
])

	.factory('upDocumentService', function($resource, Upload, deskworksConfig, userProfileCache, helper, session) {
		'use strict';

		var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/documents/:documentId'), {}, {
			update: {method: 'PUT'}
		});

		return {
			new: function(file) {
				return {
					id: helper.uid(),
					file: file,
					name: file.name,
					userAccess: !session.isAdmin(),
				};
			},
			isNew: function(doc) {
				return doc && helper.isUid(doc.id);
			},

			save: function(centerId, userId, doc) {
				var self = this;

				userProfileCache.removeAll();
				if (helper.isUid(doc.id)) {
					return Upload.upload({
						url: deskworksConfig.getApiAbsUrl('/centers/' + (centerId || 0) + '/user-profiles/' + userId + '/documents'),
						method: 'POST',
						withCredentials: true,
						data: { document: doc.file, name: doc.name }
					}).then(function(result) {
						doc.id = result.data.id;
						doc.document = result.data.document;
						delete doc.file;
						return result.data;
					});
				} else {
					return res.update({centerId: centerId || 0, userId: userId, documentId: doc.id}, self.unresolve(doc)).$promise;
				}
			},

			delete: function(centerId, userId, doc) {
				userProfileCache.removeAll();
				return res.delete({centerId: centerId || 0, userId: userId, documentId: doc.id}).$promise;
			},

			resolve: function(doc) {
				return doc;
			},

			unresolve: function(doc) {
				return {
					name: doc.name,
					userAccess: doc.userAccess,
				};
			}
		};
	});
