/**
 * Options for Task -> Permission
 */
angular.module('deskworks.optsTaskPermission', [])

  .factory('optsTaskPermission', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: null, _name: 'TASKS.PERMISSION.NONE' },
      { id: 'view', _name: 'TASKS.PERMISSION.VIEW' },
      { id: 'edit', _name: 'TASKS.PERMISSION.EDIT' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
