/**
 * Options for Settings -> General -> DashboardIncome
 */
angular.module('deskworks.optsDashboardIncome', [])

  .factory('optsDashboardIncome', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'cash', _name: 'SETUP_SETTINGS.DASHBOARD_INCOME.CASH' },
      { id: 'accrual', _name: 'SETUP_SETTINGS.DASHBOARD_INCOME.ACCRUAL' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
