export default angular.module('deskworks.calendarService', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer',
])

  .factory('calendarService', function($resource, $filter, deskworksConfig, helper, datesDeserializer) {
    const desDates = datesDeserializer([
      'reservationUnits.reservations.from', 'reservationUnits.reservations.to',
      'reservationUnits.availabilities.from', 'reservationUnits.availabilities.to'
    ]);

    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/reservations'),
      { centerId: '@centerId', from: '@from', to: '@to' },
      { query: { method: 'GET', isArray: true, transformResponse: desDates } },
    );

    return {
      query: (centerId, from, to, role) => res.query({
        centerId: centerId,
        from: $filter('date')(from, 'yyyy-MM-dd'),
        to: $filter('date')(to, 'yyyy-MM-dd'),
        role: role || undefined
      }).$promise,
    };
  });
