/**
 * Options for Credit Card Expiry Year in User Profile
 */
angular.module('deskworks.userProfile.optsCCExpiryYear', [])

  .factory('optsCCExpiryYear', function($filter) { 'use strict';
    var years = [];

    var date = new Date(), curDate = new Date();
    date.setDate(1); curDate.setDate(1); // Reset current day as 2015-06-30 may cause 2015-02-30 which is really 2015-03-02

    for(var i = 0; i < 11; i++) {
      date.setFullYear(curDate.getFullYear() + i);
      years.push({id: String(date.getFullYear()), name: $filter('date')(date, 'yyyy')});
    }

    return years;
  });
