/**
 * Options for Reservation Rules / Maximum Hours/Day/Person
 */
angular.module('deskworks.reservationRules.optsReservationLimitMax', [])

  .factory('optsReservationLimitMax', function($rootScope, $translate, $filter) { 'use strict';
    const opts = [
      { id: '0.0', _name: 'RESERVATION_RULES.UNLIMITED' },
      { id: '12.0', value: 12, unit: 'hours' },
      { id: '10.0', value: 10, unit: 'hours' },
      { id: '9.0', value: 9, unit: 'hours' },
      { id: '8.0', value: 8, unit: 'hours' },
      { id: '7.0', value: 7, unit: 'hours' },
      { id: '6.0', value: 6, unit: 'hours' },
      { id: '5.0', value: 5, unit: 'hours' },
      { id: '4.0', value: 4, unit: 'hours' },
      { id: '3.0', value: 3, unit: 'hours' },
      { id: '2.0', value: 2, unit: 'hours' },
      { id: '1.0', value: 1, unit: 'hours' },
      { id: '0.5', value: 30, unit: 'minutes' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() {
      opts.forEach(opt => {
        if (opt._name) opt.name = $translate.instant(opt._name);
        if (opt.unit) opt.name = $filter(opt.unit)(opt.value) + '/' + $filter('days')(1, true);
      });
    }

    return opts;
  });
