angular.module('deskworks.system.service', [
  'ngResource',
  'deskworks.config',
  'deskworks.helper'
])

  .factory('systemService', function($resource, $q, deskworksConfig, helper) { 'use strict';

    var resSystemEmail = $resource(deskworksConfig.getApiAbsUrl('/system-emails/:systemEmailId'), {}, {
      update: { method: 'PUT' }
    });
    var resAccessCard = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/access-cards/:accessCardId'), {}, {
      update: { method: 'PUT' }
    });
    var resLeadSource = $resource(deskworksConfig.getApiAbsUrl('/lead-sources/:leadSourceId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      /**
       * System Emails
       */
      getSystemEmails: function() {
        return resSystemEmail.query().$promise;
      },
      getSystemEmail: function(systemEmailId) {
        if(systemEmailId === 'new') return $q.when({});
        return resSystemEmail.query({}).$promise
          .then(function(result) {
            var sysEmail = helper.findByKeyValue(result, 'id', systemEmailId);
            if(sysEmail) return sysEmail;
            return $q.reject({ data: { error: 'System Email not found.' } });
          });
      },
      createSystemEmail: function(data) {
        return resSystemEmail.save({}, data).$promise;
      },
      updateSystemEmail: function(systemEmailId, data) {
        return resSystemEmail.update({ systemEmailId: systemEmailId }, data).$promise;
      },
      removeSystemEmail: function(systemEmailId) {
        return resSystemEmail.delete({ systemEmailId: systemEmailId }).$promise;
      },

      /**
       * Access Cards
       */
      getAccessCard: function(centerId, accessCardId) {
        if(accessCardId === 'new') return $q.when({ effectiveDate: moment() });
        return resAccessCard.query({ centerId: centerId }).$promise
          .then(function(result) {
            var card = helper.findByKeyValue(result, 'id', accessCardId);
            if(card) return card;
            return $q.reject({ data: { error: 'Access Card not found.' } });
          });
      },
      createAccessCard: function(centerId, data) {
        return resAccessCard.save({ centerId: centerId }, data).$promise;
      },
      updateAccessCard: function(centerId, accessCardId, data) {
        return resAccessCard.update({ centerId: centerId, accessCardId: accessCardId }, data).$promise;
      },
      removeAccessCard: function(centerId, accessCardId) {
        return resAccessCard.delete({ centerId: centerId, accessCardId: accessCardId }).$promise;
      },

      /**
       * Lead Sources
       */
      getLeadSources: function() {
        return resLeadSource.query().$promise;
      },
      getLeadSource: function(leadSourceId) {
        return resLeadSource.get({ leadSourceId: leadSourceId }).$promise;
      },
      createLeadSource: function(data) {
        return resLeadSource.save({}, data).$promise;
      },
      updateLeadSource: function(leadSourceId, data) {
        return resLeadSource.update({ leadSourceId: leadSourceId }, data).$promise;
      },
      removeLeadSource: function(leadSourceId) {
        return resLeadSource.delete({ leadSourceId: leadSourceId }).$promise;
      }

    };
  });
