angular.module('deskworks.Owner', [
  'ngResource',
  'deskworks.config',
])

  .factory('Owner', function($resource, $q, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/owners/:ownerId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: () => res.query().$promise,

      save: owner => {
        if (owner.id) return res.update({ ownerId: owner.id }, owner).$promise;
        return res.save(owner).$promise
          .then(result => { owner.id = result.id; });
      },

      delete: ownerId => res.delete({ ownerId }).$promise,
    };
  });
