angular.module('deskworks.ProductCategory', [
  'ngResource',
  'deskworks.config',
  'deskworks.helper',
])

  .factory('ProductCategory', function($resource, $q, deskworksConfig, helper) { 'use strict';
    var res = $resource(deskworksConfig.getApiAbsUrl('/product-categories/:productCategoryId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      query: function() {
        return res.query().$promise;
      },
      save: function(productCategory) {
        if(productCategory.id) return res.update({ productCategoryId: productCategory.id }, productCategory).$promise;
        return res.save(productCategory).$promise
          .then(function(result) { productCategory.id = result.id; });
      },
      delete: function(productCategoryId) {
        return res.delete({ productCategoryId: productCategoryId }).$promise;
      }
    };
  });
