/**
 * eCheck sub-service of userProfileService
 */
export default angular.module('deskworks.userProfile.service.eCheck', [
  'ngResource',
  'deskworks.helper',
  'deskworks.userProfile.cache',
  'deskworks.userProfile.optsBankAccountHolderType',
  'deskworks.userProfile.optsBankAccountType'
])

  .factory('upECheckService', function($resource, $filter, deskworksConfig, userProfileCache, helper, optsBankAccountHolderType, optsBankAccountType) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/e-checks/:eCheckId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      new: function(user) {
        return {
          id: helper.uid(),
          bankAccountHolderType: optsBankAccountHolderType[0],
          bankAccountType: optsBankAccountType[0],
          fullName: user && user.fullName,
        };
      },
      isNew: function(eCheck) {
        return eCheck && helper.isUid(eCheck.id);
      },

      save: function(centerId, userId, eCheck) {
        const self = this;

        userProfileCache.removeAll();
        if (helper.isUid(eCheck.id)) {
          return res.save({centerId: centerId || 0, userId: userId}, self.unresolve(eCheck)).$promise
            .then(result => { eCheck.id = result.id; return result; });
        }
      },

      delete: function(centerId, userId, eCheck) {
        userProfileCache.removeAll();
        return res.delete({centerId: centerId || 0, userId: userId, eCheckId: eCheck.id}).$promise;
      },

      resolve: function(eCheck) {
        eCheck.bankAccountHolderType = helper.find(optsBankAccountHolderType, eCheck.bankAccountHolderType);
        eCheck.bankAccountType = helper.find(optsBankAccountType, eCheck.bankAccountType);

        return eCheck;
      },

      unresolve: function(eCheck) {
        return {
          bankAccountHolderType: eCheck.bankAccountHolderType.id,
          bankAccountType: eCheck.bankAccountType.id,
          bankName: eCheck.bankName,
          bankRoutingNumber: eCheck.bankRoutingNumber,
          bankAccountNumber: eCheck.bankAccountNumber,
          fullName: eCheck.fullName,
        };
      }
    };
  });
