export default angular.module('deskworks.emailSiteMembers.service', [
  'ngResource',
  'deskworks.config',
])

  .factory('emailSiteMembers', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/email-site-members'));
    const resTpl = $resource(deskworksConfig.getApiAbsUrl('/email-templates/email-site-members'), {}, { update: { method: 'PUT' } });

    return {
      send: (centerId, data, test) => res.save({ centerId, test: test || null }, data).$promise,

      query: (centerId, membershipCategoryIds, checkedInOnly) => {
        membershipCategoryIds = membershipCategoryIds && membershipCategoryIds.join();
        return res.query({ centerId, membershipTypeIds: membershipCategoryIds, checkedInOnly }).$promise;
      },

      getTpl: () => resTpl.get().$promise,

      saveTpl: (data) => resTpl.update({}, data).$promise,
    };
  });
