angular.module('deskworks.allowedHost.service', [
  'ngResource',
  'deskworks.config',
  'deskworks.helper',
])

  .factory('allowedHostService', function($resource, $q, deskworksConfig, helper) { 'use strict';
    var res = $resource(deskworksConfig.getApiAbsUrl('/setup/allowed-hosts/:allowedHostId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      query: function() {
        return res.query().$promise;
      },
      save: function(allowedHost) {
        if(allowedHost.id) return res.update({ allowedHostId: allowedHost.id }, allowedHost).$promise;
        return res.save(allowedHost).$promise
          .then(function(result) { allowedHost.id = result.id; });
      },
      delete: function(allowedHostId) {
        return res.delete({ allowedHostId: allowedHostId }).$promise;
      }
    };
  });
