angular.module('deskworks.Credentials', [
  'ngResource',
  'deskworks.config',
])

  .factory('Credentials', function($resource, $timeout, deskworksConfig) { 'use strict';
    const res = $resource(deskworksConfig.getApiAbsUrl('/credentials/:login'));

    return {
      get: login => res.get({ login }).$promise
        .then(cr => {
          cr.login = login;
          return cr;
        }),
    };
  });
