angular.module('deskworks.CheckedInUsers', [
  'ngResource',
  'deskworks.config'
])

  .factory('CheckedInUsers', function($resource, deskworksConfig) { 'use strict';
    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/checked-in-users'));

    return {
      query: function(centerId) {
        return res.query({ centerId: centerId }).$promise;
      },
    };
  });
