angular.module('deskworks.Region', [
  'ngResource',
  'deskworks.config',
])

  .factory('Region', function($resource, $q, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/regions/:regionId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: () => res.query().$promise,

      save: region => {
        if (region.id) return res.update({ regionId: region.id }, region).$promise;
        return res.save(region).$promise
          .then(result => { region.id = result.id; });
      },

      delete: regionId => res.delete({ regionId }).$promise,
    };
  });
