/**
 * Options for Setup -> Settings -> Pro-rate at Start
 */
angular.module('deskworks.setup.optsProrateAtStart', [])

  .factory('optsProrateAtStart', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: null, _name: 'GLOBAL.NONE' },
      { id: 'firstCharge', _name: 'SETUP_SETTINGS.PRO_RATE_AT_START.FIRST_MONTH' },
      { id: 'secondCharge', _name: 'SETUP_SETTINGS.PRO_RATE_AT_START.SECOND_MONTH' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
