export default angular.module('deskworks.notifications.service', [
  'ngResource',
  'deskworks.config',
  'deskworks.helper'
])

  .factory('Notifications', function($resource, $timeout, deskworksConfig, helper) {
    const methods = { update: { method: 'PUT' } };

    const res = $resource(deskworksConfig.getApiAbsUrl('/notifications/:id'), {}, methods);
    const resTpl = $resource(deskworksConfig.getApiAbsUrl('/email-templates/:tplId'), {}, methods);
    const resTplAtt = $resource(deskworksConfig.getApiAbsUrl('/email-templates/:tplId/attachments/:id'), {}, methods);

    var resBulkInvoices = $resource(deskworksConfig.getApiAbsUrl('/setup/bulk-invoices'));
    var resRecurringInvoices = $resource(deskworksConfig.getApiAbsUrl('/setup/recurring-invoices'));
    var resExpiredCardEmail = $resource(deskworksConfig.getApiAbsUrl('/setup/expired-card-emails'));
    var resDeclinedCardEmail = $resource(deskworksConfig.getApiAbsUrl('/setup/declined-card-emails'));
    var resWelcomeLetter = $resource(deskworksConfig.getApiAbsUrl('/setup/welcome-letters'));
    var resWelcomeLetterAtt = $resource(deskworksConfig.getApiAbsUrl('/setup/welcome-letters/123/attachments/:attachmentId'));
    var resResConfirmation = $resource(deskworksConfig.getApiAbsUrl('/setup/reservation-confirmations'));
    var resResConfirmationAtt = $resource(deskworksConfig.getApiAbsUrl('/setup/reservation-confirmations/123/attachments/:attachmentId'));
    var resCalSyncError = $resource(deskworksConfig.getApiAbsUrl('/setup/calendar-sync-error-emails'));
    var resPassesUsedUp = $resource(deskworksConfig.getApiAbsUrl('/setup/passes-used-up-emails'));
    var resBucketUsedUp = $resource(deskworksConfig.getApiAbsUrl('/setup/bucket-used-up-emails'));
    const resMsgBoard = $resource(deskworksConfig.getApiAbsUrl('/dw-mail/message-board/templates/:templateId'), {}, methods);
    const resDeskTablet = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/desk-tablet-template'), {}, methods);

    return {
      get: function(id) {
        return res.get({ id }).$promise;
      },
      save: function(id, data) {
        return res.update({ id }, data).$promise;
      },

      getEmailTemplate: (tplId) => resTpl.get({ tplId }).$promise,
      saveEmailTemplate: (tplId, data) => resTpl.update({ tplId }, data).$promise,
      attachEmailTemplate: (tplId, data) =>resTplAtt.save({ tplId }, data).$promise,
      detachEmailTemplate: (tplId, id) => resTplAtt.delete({ tplId, id }).$promise,

      /**
       * Bulk Invoices
       */
      getBulkInvoices: function() {
        return resBulkInvoices.get({}).$promise;
      },
      saveBulkInvoices: function(data) {
        return resBulkInvoices.save({}, data).$promise;
      },

      /**
       * Recurring Invoices
       */
      getRecurringInvoices: function() {
        return resRecurringInvoices.get({}).$promise;
      },
      saveRecurringInvoices: function(data) {
        return resRecurringInvoices.save({}, data).$promise;
      },

      /**
       * Expired Card Email
       */
      getExpiredCardEmail: function() {
        return resExpiredCardEmail.get({}).$promise;
      },
      saveExpiredCardEmail: function(data) {
        return resExpiredCardEmail.save({}, data).$promise;
      },

      /**
       * Declined Card Email
       */
      getDeclinedCardEmail: function() {
        return resDeclinedCardEmail.get({}).$promise;
      },
      saveDeclinedCardEmail: function(data) {
        return resDeclinedCardEmail.save({}, data).$promise;
      },

      /**
       * Welcome Letter
       */
      getWelcomeLetter: function() {
        return resWelcomeLetter.get({}).$promise;
      },
      saveWelcomeLetter: function(data) {
        return resWelcomeLetter.save({}, data).$promise;
      },
      attachWelcomeLetter: function(data) {
        return resWelcomeLetterAtt.save({}, data).$promise;
      },
      detachWelcomeLetter: function(attachmentId) {
        return resWelcomeLetterAtt.delete({ attachmentId: attachmentId }).$promise;
      },

      /**
       * Calendar Sync Error Email
       */
      getCalSyncErrorEmail: function() {
        return resCalSyncError.get({}).$promise;
      },
      saveCalSyncErrorEmail: function(data) {
        return resCalSyncError.save({}, data).$promise;
      },

      /**
       * Passes Used Up
       */
      getPassesUsedUpEmail: function() {
        return resPassesUsedUp.get({}).$promise;
      },
      savePassesUsedUpEmail: function(data) {
        return resPassesUsedUp.save({}, data).$promise;
      },

      /**
       * Bucket Used Up
       */
      getBucketUsedUpEmail: function() {
        return resBucketUsedUp.get({}).$promise;
      },
      saveBucketUsedUpEmail: function(data) {
        return resBucketUsedUp.save({}, data).$promise;
      },

      /**
       * Reservation Confirmation
       */
      getReservationConfirmation: function() {
        return resResConfirmation.get({}).$promise;
      },
      saveReservationConfirmation: function(data) {
        return resResConfirmation.save({}, data).$promise;
      },
      attachReservationConfirmation: function(data) {
        return resResConfirmationAtt.save({}, data).$promise;
      },
      detachReservationConfirmation: function(attachmentId) {
        return resResConfirmationAtt.delete({ attachmentId: attachmentId }).$promise;
      },

      /**
       * Message Board Notices
       */
      getMessageBoardEmailTemplate: (templateId) => resMsgBoard.get({ templateId }).$promise,
      saveMessageBoardEmailTemplate: (templateId, data) => resMsgBoard.update({ templateId }, data).$promise,

      getDeskTabletTemplate: (centerId) => resDeskTablet.get({ centerId }).$promise,
      saveDeskTabletTemplate: (centerId, data) => resDeskTablet.update({ centerId }, data).$promise,
    };
  });
