angular.module('deskworks.usageEntryService', [
  'ngResource',
  'deskworks.config',
  'datesDeserializer'
])

  .factory('usageEntryService', function($resource, $q, $filter, deskworksConfig, helper, datesDeserializer) { 'use strict';
    var fdate = $filter('date');
    var desDates = datesDeserializer(['usages.checkInTime', 'usages.checkOutTime']);

    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/usage-entries/:usageId'), {}, {
      query: { method: 'GET', isArray: false, transformResponse: [angular.fromJson, desDates] },
      save: { method: 'POST', transformRequest: [serializer, angular.toJson] },
      update: { method: 'PUT', transformRequest: [serializer, angular.toJson] }
    });

    function serializer(data) {
      data = angular.extend({}, data);
      data.checkInTime = fdate(data.checkInTime, 'iso');
      data.checkOutTime = fdate(data.checkOutTime, 'iso');
      return data;
    }

    return {
      query: function(centerId, userId, page, pageSize) {
        return res.query({ userId: userId, centerId: centerId, page: page, pageSize: pageSize }).$promise;
      },

      get: function(centerId, userId, usageId) {
        if(usageId === 'new') {
          return $q.when({
            id: 'new',
            checkInTime: moment(),
            checkOutTime: moment(),
            notes: ''
          });
        }

        return res.query({ userId: userId, centerId: centerId }).$promise
          .then(function(usages) { // as we have only get usages array operation, we have to find necessary usage by id
            var usage = helper.findByKeyValue(usages.usages, 'id', usageId);
            if(usage) return usage;
            return $q.reject({ data: { error: 'Failed to find usage with id=' + usageId } });
          });
      },

      save: function(centerId, userId, usage) {
        if(!usage.id || usage.id === 'new') return res.save({ userId: userId, centerId: centerId }, usage).$promise;
        return res.update({ userId: userId, centerId: centerId, usageId: usage.id }, usage).$promise;
      },

      delete: function(centerId, userId, usageId) {
        return res.delete({ userId: userId, centerId: centerId, usageId: usageId }).$promise;
      }
    };
  });
