/**
 * Options for User Profile -> Membership Billing Method
 */
export default angular.module('deskworks.userProfile.optsBillingMethod', [])

  .factory('optsBillingMethod', function($rootScope, $translate) {
    const opts = [
      { id: 'cc', _name: 'BILLING_PROFILE.BILLING_METHOD.CC', import: 'Credit Card', paymentMethodKey: 'creditCards' },
      { id: 'ach', _name: 'BILLING_PROFILE.BILLING_METHOD.ACH', import: 'ACH/EFT/Direct Debit', paymentMethodKey: 'eChecks' },
      { id: 'emailInvoice', _name: 'BILLING_PROFILE.BILLING_METHOD.EMAIL_INVOICE', import: 'Email Invoice', restricted: true },
      { id: 'paperInvoice', _name: 'BILLING_PROFILE.BILLING_METHOD.PAPER_INVOICE', import: 'Paper Invoice', restricted: true },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(opt => { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
