/**
 * Options for Reservation Rules / Minimum Reservable Time
 */
angular.module('deskworks.reservationRules.optsReservationLimitMin', [])

  .factory('optsReservationLimitMin', function($rootScope, $translate, $filter) { 'use strict';
    const opts = [
      { id: '0.25', value: 15, unit: 'minutes', name: '15 min' },
      { id: '0.5', value: 30, unit: 'minutes', name: '30 min' },
      { id: '1.0', value: 1, unit: 'hours', name: '1 hr' },
      { id: '2.0', value: 2, unit: 'hours', name: '2 hr' },
      { id: '4.0', value: 4, unit: 'hours', name: '4 hr' },
      { id: '24.0', value: 1, unit: 'days', name: '1 day' },
      { id: '72.0', value: 3, unit: 'days', name: '3 day' },
      { id: '168.0', value: 1, unit: 'weeks', name: '1 week' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(opt => { opt.name = $filter(opt.unit)(opt.value); }); }

    return opts;
  });
