export default angular.module('deskworks.NetCheckInSettings', [
  'ngResource',
  'deskworks.config',
])

  .factory('NetCheckInSettings', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/net-check-in/settings/:centerId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: function(centerId) {
        return res.get({ centerId }).$promise;
      },

      save: function(centerId, data) {
        return res.update({ centerId }, data).$promise;
      },
    };
  });
