/**
 * Options for Credit Card Expiry Month in User Profile
 */
angular.module('deskworks.userProfile.optsCCExpiryMonth', [])

  .factory('optsCCExpiryMonth', function($filter, $rootScope) { 'use strict';
    var months = [];

    var date = new Date(), curDate = new Date();
    date.setDate(1); curDate.setDate(1); // Reset current day as 2015-06-30 may cause 2015-02-30 which is really 2015-03-02

    for(var i = 0; i < 12; i++) {
      date.setMonth(i);
      months.push({ id: ((i + 1) < 10 ? '0' + (i + 1) : '' + (i + 1)), name: $filter('date')(date, 'MM - LLLL') });
    }

    $rootScope.$on('$setLocaleSuccess', function() {
      months.forEach(function(month, i) {
        date.setMonth(i);
        month.name = $filter('date')(date, 'MM - LLLL');
      });
    });

    return months;
  });
