/**
 * Phone sub-service of userProfileService
 */
angular.module('deskworks.userProfile.service.phone', [
	'ngResource',
	'deskworks.helper',
	'deskworks.userProfile.cache',
	'deskworks.userProfile.optsPhoneType'
])

	.factory('upPhoneService', function($resource, deskworksConfig, userProfileCache, helper, optsPhoneType) {
		'use strict';

		var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/phones/:phoneId'), {}, {
			update: {method: 'PUT'}
		});

		return {
			new: function() {
				return {
					id: helper.uid(),
					type: optsPhoneType[2]
				};
			},
			isNew: function(phone) {
				return phone && helper.isUid(phone.id);
			},

			save: function(centerId, userId, phone) {
				var self = this;

				userProfileCache.removeAll();
				if(helper.isUid(phone.id)) {
					return res.save({centerId: centerId || 0, userId: userId}, self.unresolve(phone)).$promise.then(function(result) {
						phone.id = result.id;
						return result;
					});
				} else {
					return res.update({centerId: centerId || 0, userId: userId, phoneId: phone.id}, self.unresolve(phone)).$promise;
				}
			},

			delete: function(centerId, userId, phone) {
				userProfileCache.removeAll();
				return res.delete({centerId: centerId || 0, userId: userId, phoneId: phone.id}).$promise;
			},

			resolve: function(phone) {
				phone.type = helper.findByKeyValue(optsPhoneType, 'id', phone.typeId);
				return phone;
			},

			unresolve: function(phone) {
				return {
					typeId: phone.type && phone.type.id ? phone.type.id : phone.type,
					number: phone.number,
					ext: phone.ext
				};
			}
		};
	});
