/**
 * Options for Reservation Rules / Time in Advance Reservations Required
 */
angular.module('deskworks.reservationRules.optsTimeInAdvance', [])
  .factory('optsTimeInAdvance', function($rootScope, $translate, $filter) { 'use strict';
    const opts = [
      { id: 0, _name: 'GLOBAL.NONE' },
      { id: 1 },
      { id: 12 },
      { id: 24 },
      { id: 48 },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() {
      opts.forEach(opt => {
        opt.name = opt._name ? $translate.instant(opt._name) : $filter('hours')(opt.id);
      });
    }

    return opts;
  });
