/**
 * Mailbox sub-service of userProfileService
 */
angular.module('deskworks.userProfile.service.mailbox', [
	'ngResource',
	'deskworks.helper',
	'deskworks.userProfile.cache',
	'deskworks.userProfile.optsMailboxHandling'
])

	.factory('upMailboxService', function($resource, deskworksConfig, userProfileCache, helper, optsMailboxHandling) {
		'use strict';

		var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/mailboxes/:mailboxId'), {}, {
			update: {method: 'PUT'}
		});

		return {
			new: function() {
				return {
					id: helper.uid(),
					handling: optsMailboxHandling[0]
				};
			},
			isNew: function(mailbox) {
				return mailbox && helper.isUid(mailbox.id);
			},

			save: function(centerId, userId, mailbox) {
				var self = this;

				userProfileCache.removeAll();
				if(helper.isUid(mailbox.id)) {
					return res.save({centerId: centerId || 0, userId: userId}, self.unresolve(mailbox)).$promise.then(function(result) {
						mailbox.id = result.id;
						return result;
					});
				} else {
					return res.update({centerId: centerId || 0, userId: userId, mailboxId: mailbox.id}, self.unresolve(mailbox)).$promise;
				}
			},

			delete: function(centerId, userId, mailbox) {
				userProfileCache.removeAll();
				return res.delete({centerId: centerId || 0, userId: userId, mailboxId: mailbox.id}).$promise;
			},

			resolve: function(mailbox) {
				mailbox.handling = helper.findByKeyValue(optsMailboxHandling, 'id', mailbox.handling);
				return mailbox;
			},

			unresolve: function(mailbox) {
				return {
					number: mailbox.number,
					key: mailbox.key,
					names: mailbox.names,
					handling: mailbox.handling ? mailbox.handling.id : mailbox.handling
				};
			}
		};
	});
