export default angular.module('deskworks.WhiteLabelLinks', [
  'ngResource',
  'deskworks.config',
])

  .factory('WhiteLabelLinks', function ($resource, Upload, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/white-label/links'), {}, {
      get: { method: 'GET' },
      update: { method: 'PUT' },
    });

    return {
      get: () => res.get().$promise,
      save: data => res.update(data).$promise,

      uploadTermsOfService: file => {
        return Upload.upload({
          url: deskworksConfig.getApiAbsUrl('/white-label/links'),
          method: 'PUT',
          withCredentials: true,
          data: { termsOfService: file },
        });
      },

      uploadReservationAgreement: file => {
        return Upload.upload({
          url: deskworksConfig.getApiAbsUrl('/white-label/links'),
          method: 'PUT',
          withCredentials: true,
          data: { reservationAgreement: file },
        });
      },
    };
  });
