/**
 * Options for Invoice Date in Setup / Settings
 */
angular.module('deskworks.setup.optsInvoiceDate', [])

  .factory('optsInvoiceDate', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'invoiceDate', _name: 'SETUP_SETTINGS.INVOICE_DATE.INVOICE_DATE' },
      { id: 'chargeDate', _name: 'SETUP_SETTINGS.INVOICE_DATE.CHARGE_DATE' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
