/**
 * Options for Bank Account Holder Type in Billing Profile / eChecks
 */
angular.module('deskworks.userProfile.optsBankAccountHolderType', [])

  .factory('optsBankAccountHolderType', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'personal', _name: 'BILLING_PROFILE.E_CHECKS.ACCOUNT_HOLDER_TYPE.PERSONAL' },
      { id: 'business', _name: 'BILLING_PROFILE.E_CHECKS.ACCOUNT_HOLDER_TYPE.BUSINESS' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
