/**
 * Options for 'Reservation Credits Unit' in Setup / Settings
 */
angular.module('deskworks.setup.optsReservationCreditsUnit', [])

  .factory('optsReservationCreditsUnit', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: '$', _name: 'SETUP_SETTINGS.RESERVATION_CREDITS_UNIT.MONEY' },
      { id: 'hours', _name: 'SETUP_SETTINGS.RESERVATION_CREDITS_UNIT.HOURS' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
