angular.module('deskworks.PassProduct.service', [
  'ngResource',
  'deskworks.config'
])

  .factory('PassProduct', function($resource, $q, deskworksConfig) { 'use strict';
    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/pass-products'));
    return {
      query: function(centerId) {
        return res.query({ centerId: centerId }).$promise;
      }
    };
  });
