angular.module('deskworks.checkInLog.service', [
  'ngResource',
  'deskworks.config'
])
  .factory('checkInLogService', function($resource, $filter, deskworksConfig, helper) { 'use strict';
    var toDate = $filter('toDate');

    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/logs/:logId'), {}, {
      update: { method: 'PUT' }
    });

    return {
      getLog: function(centerId, userId, page, pageSize, macAddress) {
        return res.get({ centerId: centerId, userId: userId, page: page, pageSize: pageSize, macAddress: macAddress }).$promise
          .then(function(logData) {
            logData.checkInTime = toDate(logData.checkInTime);
            logData.log.forEach(function(rec) {
              rec.time = toDate(rec.time);
            });
            return logData;
          });
      },

      updateLog: function(centerId, userId, logId, data) {
        return res.update({ centerId: centerId, userId: userId, logId: logId }, data).$promise;
      },

      deleteLog: function(centerId, userId, logId) {
        return res.delete({ centerId: centerId, userId: userId, logId: logId }).$promise;
      },

      checkInOut: function(centerId, userId, operation, time) {
        time = $filter('date')(time, 'iso');
        return res.save({ centerId: centerId, userId: userId }, { operation: operation, time: time }).$promise;
      }
    };
  });
