angular.module('deskworks.CheckedInMembers', [
  'ngResource',
  'deskworks.config'
])
  .factory('CheckedInMembers', function($resource, $timeout, $filter, deskworksConfig, helper) { 'use strict';
    var res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/checked-in-members'), {}, {});

    return {
      query: function(centerId) {
        return res.query({ centerId: centerId }).$promise;
      },
    };
  });
