/**
 * Options for Setup -> Settings -> Pro-rate at Termination
 */
angular.module('deskworks.setup.optsProrateAtTermination', [])

  .factory('optsProrateAtTermination', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: null, _name: 'GLOBAL.NONE' },
      { id: 'noticeDate', _name: 'SETUP_SETTINGS.PRO_RATE_AT_TERMINATION.NOTICE_DATE' },
      { id: 'terminationDate', _name: 'SETUP_SETTINGS.PRO_RATE_AT_TERMINATION.TERMINATION_DATE' }
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
