/**
 * AccessCard service
 */
angular.module('deskworks.accessCard.service', [
    'ngResource',
    'deskworks.config'
  ])

  // TODO: API doesn't support single GET method, use query GET and then find necessary item

  .factory('AccessCard', function($resource, deskworksConfig) { 'use strict';
    var url = deskworksConfig.getApiAbsUrl('/centers/:centerId/access-cards/:accessCardId');

    return $resource(url, { centerId: '@centerId', accessCardId: '@accessCardId' }, {
      update: { method: 'PUT' }
    });
  });
