angular.module('session', [
  'deskworks.config',
  'deskworks.userRoles',
])

  .factory('session', function($q, $http, deskworksConfig, userRoles) { 'use strict';
    const url = deskworksConfig.getApiAbsUrl('/auth');

    const session = {
      wait,
      refresh,
      login,
      logout,
      isAnonymous,
      isAdmin,
      isRole,
      loginSilent,
    };

    reset();

    let promise = refresh() // initial attempt to restore session on startup
      .finally(function() { promise = null; });

    return session;

    function isAnonymous() {
      return !session.user;
    }

    function isAdmin() {
      return session.user && userRoles[session.user.role] && userRoles[session.user.role].type === 'admin' || false;
    }

    function isRole(roles) {
      if (!angular.isArray(roles)) roles = [roles];
      return session.user && roles.some(role => role === session.user.role) || false;
    }

    function reset() {
      session.accessToken = null;
      session.user = null;
      session.expireAt = null;
    }

    function wait() {
      return $q.when(promise);
    }

    /**
     * Refresh session: exchange long-life refreshToken (auth cookie) to short-life JWT accessToken
     */
    function refresh() {
      if (promise) return promise;
      promise = $http.get(url)
        .then(onSession)
        .catch(err => { if (err.status !== 401) throw err; })  // anonymous is ok
        .finally(() => { promise = null; });
      return promise;
    }

    /**
     * Create session: log in
     */
    function login(username, password, remember) {
      return $q.when(promise)
        .finally(() => {
          promise = $http.post(url, { username: username, password: password, remember: remember })
            .then(onSession)
            .finally(() => { promise = null; });
          return promise;
        });
    }

    /**
     * Create short-time session (log in) without visible effects (used in Reservation Tablet wizard)
     */
    function loginSilent(username, password) {
      return $http.post(url, { username, password })
        .then(result => {
          session.accessToken = result.data.accessToken;
          return result;
        });
    }

    /**
     * Destroy session: log out
     */
    function logout() {
      return $q.when(promise)
        .finally(() => $http.delete(url).finally(reset));
    }

    /**
     * Session create/refresh handler
     */
    function onSession(result) {
      session.accessToken = result.data.accessToken;
      session.user = result.data.user;
      return result;
    }
  });
