/**
 * Options for Administrator -> Permissions -> Permission
 */
angular.module('deskworks.optsAdminPermission', [])

  .factory('optsAdminPermission', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: null, _name: 'ADMINISTRATORS.PERMISSION.NONE' },
      { id: 'all', _name: 'ADMINISTRATORS.PERMISSION.ALL' },
      { id: 'viewProfileAndCalendar', _name: 'ADMINISTRATORS.PERMISSION.VIEW_PROFILE_AND_CALENDAR' },
    ];

    translate();
    $rootScope.$on('$setTranslationsSuccess', translate);
    function translate() { opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); }); }

    return opts;
  });
