/**
 * Options for Bank Account Type in Billing Profile / eChecks
 */
angular.module('deskworks.userProfile.optsBankAccountType', [])

  .factory('optsBankAccountType', function($rootScope, $translate) { 'use strict';
    var opts = [
      { id: 'checking', _name: 'BILLING_PROFILE.E_CHECKS.ACCOUNT_TYPE.CHECKING' },
      { id: 'savings', _name: 'BILLING_PROFILE.E_CHECKS.ACCOUNT_TYPE.SAVINGS' }
    ];

    $rootScope.$on('$setTranslationsSuccess', function() {
      opts.forEach(function(opt) { opt.name = $translate.instant(opt._name); });
    });

    return opts;
  });
