export default angular.module('deskworks.emailAllMembers.service', [
  'ngResource',
  'deskworks.config',
])

  .factory('emailAllMembers', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/email-all-members'));
    const resTpl = $resource(deskworksConfig.getApiAbsUrl('/email-templates/email-all-members'), {}, { update: { method: 'PUT' } });

    return {
      send: (data, test) => res.save({ test: test || null }, data).$promise,

      query: (includeNonMembers) => res.query({ includeNonMembers }).$promise,

      getTpl: () => resTpl.get().$promise,

      saveTpl: (data) => resTpl.update({}, data).$promise,
    };
  });
