export default angular.module('deskworks.userProfile.service', [
  'ngResource',
  'ngFileUpload',
  'deskworks.config',
  'deskworks.userProfile.cache',
  'deskworks.helper',
  'deskworks.system.service',
  'deskworks.userProfile.service.photo',
  'deskworks.userProfile.service.address',
  'deskworks.userProfile.service.phone',
  'deskworks.userProfile.service.printerCode',
  'deskworks.userProfile.service.document',
  'deskworks.userProfile.service.coMember',
  'deskworks.userProfile.service.mailbox',
  'deskworks.userProfile.service.creditCard',
  'deskworks.userProfile.service.eCheck',
  'deskworks.UserProfileFields',
  'deskworks.userProfile.optsMembershipStatus',
  'deskworks.userProfile.optsBillingMethod',
  'deskworks.userProfile.optsCCExpiryMonth',
  'deskworks.userProfile.optsCCExpiryYear',
])

  .factory('userProfileService', function(
    $resource, $q, $timeout, $filter, dwAlerts, Upload, deskworksConfig, userProfileCache, helper,
    upPhotoService, upAddressService, upPhoneService, upPrinterCodeService,
    upDocumentService, upCoMemberService, upMailboxService, upCreditCardService, systemService,
    upECheckService, optsMembershipStatus, optsBillingMethod, optsCCExpiryMonth, optsCCExpiryYear,
    UserProfileFields) {

    // General URL for GET user profiles
    const res = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId'), {}, {
      get:    { method: 'GET', cache: userProfileCache },
      query:  { method: 'GET', isArray: false }
    });

    // Explicit URL for POST/PUT member profile
    const resMember = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/members/:userId'), {}, {
      get:    { method: 'GET', cache: userProfileCache },
      update: { method: 'PUT' }
    });

    // Explicit URL for POST/PUT non-member profile
    const resNonMember = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/non-members/:userId'), {}, {
      get:    { method: 'GET', cache: userProfileCache },
      update: { method: 'PUT' }
    });

    // Restore Password
    const resRestorePassword = $resource(deskworksConfig.getApiAbsUrl('/restore-password'), {}, {
      update: { method: 'PUT' }
    });

    const resGdpr = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/user-profiles/:userId/gdpr'));

    /**
     * Get resource for saving user profile. Analyzes user.membershipStatus and returns either resMember or resNonMember
     */
    function getResSave(user) {
      if(user && user.membershipStatus && (user.membershipStatus.id === 'active' || user.membershipStatus.id === 'inactive')) {
        return resMember;
      }
      return resNonMember;
    }

    return {
      /**
       * Child Entities
       */
      phone: upPhoneService,
      address: upAddressService,
      printerCode: upPrinterCodeService,
      document: upDocumentService,
      photo: upPhotoService,
      creditCard: upCreditCardService,
      eCheck: upECheckService,
      coMember: upCoMemberService,
      mailbox: upMailboxService,

      /**
       * Reset user profile cache
       */
      resetCache: function() {
        userProfileCache.removeAll();
      },

      /**
       * Get new user profile template (requests template from API)
       */
      new: function(center, role, scenario) {
        const self = this;
        const res = role === 'member' ? resMember : resNonMember;
        return res.get({ centerId: center.id || 0, userId: 'new', scenario: scenario }).$promise
          .then(result => {
            result.id = helper.uid();
            result.isNew = function() { return self.isNew(this); };

            self.resolve(result);

            result.addresses = (role === 'nonMember' && scenario === 'register') ? [] : [upAddressService.new(center.countryId, center.state)];
            result.phones = [upPhoneService.new()];
            result.printerCodes = [];
            result.documents = [];
            result.coMembers = [];
            result.mailboxes = [];
            result.accessCards = [];
            result.billingMethod = result.billingMethod || optsBillingMethod[0].id;

            if (role === 'member' && scenario === 'register') {
              result.membershipStatus = helper.find(optsMembershipStatus, 'active');
              result.membershipStartDate = moment();
            }

            return result;
          });
      },

      /**
       * Check if user profile is just a new template, not saved to backend
       */
      isNew: function(userProfile) {
        return userProfile && helper.isUid(userProfile.id);
      },

      /**
       * Create/Update user profile
       */
      save: function(centerId, user, registration) {
        const self = this;

        userProfileCache.removeAll();
        if (helper.isUid(user.id) || !user.id) {
          return getResSave(user).save({ centerId: centerId || 0, registration: registration }, self.unresolve(user)).$promise.then(function(result) {
            user.id = result.id;
            return result;
          });
        } else {
          return getResSave(user).update({ centerId: centerId || 0, userId: user.id }, self.unresolve(user)).$promise;
        }
      },

      /**
       * Update raw chunk of user profile data without unresolving
       */
      updateRaw: function(centerId, user, data) {
        userProfileCache.removeAll();
        return getResSave(user).update({ centerId: centerId || 0, userId: user.id }, data).$promise;
      },

      /**
       * Get resolved user profile
       */
      get: function(centerId, userId) {
        var self = this;
        return res.get({ centerId: centerId || 0, userId: userId }).$promise
          .then(result => {
            result.schema = result.schema || {};
            return self.resolve(result);
          });
      },

      /**
       * Get resolved user profile - special case for register/signUp scenarios
       */
      getForRegistration: function(centerId, userId, role, scenario) {
        const self = this;
        const res = role === 'member' ? resMember : resNonMember;
        return res.get({ centerId: centerId || 0, userId: userId, scenario: scenario }).$promise
          .then(result => {
            result.billingMethod = result.billingMethod || optsBillingMethod[0].id;
            return self.resolve(result);
          });
      },

      /**
       * Search users
       */
      search: function(centerId, data) {
        data.joinDate = $filter('date')(data.joinDate, 'yyyy-MM-dd');
        data = angular.extend({ pageSize: 100, sort: 'lastName', order: 1 }, data);
        data.centerId = centerId || 0; // Add centerId to other params
        return res.query(data).$promise
          .then(result => {
            result.users.forEach(user => {
              user.fullName = user.fullName || helper.join([user.firstName, user.lastName], ' ');
              user.login = helper.stripUid(user.login);
              user.email = helper.stripUid(user.email);
              user.joinDate = $filter('toDate')(user.joinDate);
            });
            return result;
        });

      },

      /**
       * Query user profiles / search users
       */
      query: function(centerId, data) {
        data = angular.extend({ pageSize: 0 }, data);
        return this.search(centerId, data)
          .then(result => result.users);
      },

      /**
       * Quick search user profiles
       */
      quickSearch: function(centerId, quickSearch) {
        var data = { centerId: centerId || 0, quickSearch: quickSearch, pageSize: 50, activeMembers: true, nonMembers: true };
        return this.search(centerId, data)
          .then(result => result.users);
      },

      /**
       * Restore Password
       */
      restorePassword: function(data) {
        userProfileCache.removeAll();
        return resRestorePassword.save({}, data).$promise;
      },

      /**
       * Delete user profile
       */
      delete: function(centerId, userId) {
        return res.delete({ centerId: centerId || 0, userId: userId }).$promise;
      },

      /**
       * Delete GDPR data
       */
      deleteGdpr: function(centerId, userId) {
        userProfileCache.removeAll();
        return resGdpr.delete({ centerId: centerId || 0, userId: userId }).$promise;
      },

      /**
       * Pre-process user profile, convert string datetime to Date objects, strip uids, etc.
       */
      resolve: function(user) {
        const self = this;

        user.fullName = helper.join([user.firstName, user.lastName], ' ');
        user.fullNameCompany = helper.join([user.company, user.fullName], ', ');
        user.login = helper.stripUid(user.login);
        user.email = helper.stripUid(user.email);
        user.joinDate = $filter('toDate')(user.joinDate);
        user.membershipStatus = helper.find(optsMembershipStatus, user.membershipStatus);
        user.membershipPriceList = helper.find((user.options || {}).membershipPriceLists, user.membershipPriceListId);
        user.leadSource = helper.find((user.options || {}).leadSources, user.leadSourceId);

        (user.addresses || []).forEach(address => {
          upAddressService.resolve(address);
          if (address.type && address.type.id === 4 && !user.billingAddress) {
            user.billingAddress = address; // collect billing address
          }
        });
        if (!user.billingAddress && user.addresses && user.addresses.length > 0) {
          user.billingAddress = user.addresses[0]; // if no billing address, take the first one
        }
        (user.phones || []).forEach(phone => { upPhoneService.resolve(phone); });
        (user.coMembers || []).forEach(coMember => { self.coMember.resolve(coMember); });
        (user.mailboxes || []).forEach(mailbox => { self.mailbox.resolve(mailbox); });
        (user.creditCards || []).forEach(cc => { self.creditCard.resolve(cc); });
        (user.eChecks || []).forEach(eCheck => { self.eCheck.resolve(eCheck); });
        user.isNew = function() { // inject isNew method to user profile
          return self.isNew(this);
        };
        return user;
      },

      /**
       * Convert resolved user profile to data ready for POST/PUT
       */
      unresolve: function(user) {
        const data = {
          login: user.login,
          password: user.password,
          passwordConfirmation: user.passwordConfirmation,
          email: user.email,
          firstName: user.firstName,
          lastName: user.lastName,
          middleName: user.middleName,
          occupation: user.occupation,
          company: user.company,
          leadSourceId: (user.leadSource || {}).id,
          comments: user.comments,
          membershipPriceListId: (user.membershipPriceList || {}).id,
          membershipProductId: (user.membershipProduct || {}).id,
          membershipStatus: (user.membershipStatus || {}).id,
          membershipStartDate: $filter('date')(user.membershipStartDate, 'yyyy-MM-dd'),
        };
        if (user.discountCode) data.discountCode = user.discountCode;
        return data;
      },


      /**
       * Binds test user data generator on pressing Alt + T to prevent wasting time on typing.
       * First Name, Last Name, Email, Login are unique with timestamp, password is 123456.
       */
      bindTestUserGenerator: function($scope, userProfile, reg) {
        document.addEventListener('keydown', fillTestUserData);
        $scope.$on('$destroy', function() {
          document.removeEventListener('keydown', fillTestUserData);
        });

        function fillTestUserData(event) {
          // Alt + T
          if(event.altKey && event.keyCode === 84) {
            const uid = 'test' + moment().unix();

            userProfile.firstName = uid;
            userProfile.lastName = uid;
            userProfile.email = uid + '@z5ut48g83bf04bvg93f.com';

            if (!reg || reg.member || reg.selfRegistration) {
              userProfile.login = uid;
              userProfile.password = 'Zx123456';
              userProfile.passwordConfirmation = 'Zx123456';
            }

            if (userProfile.addresses && userProfile.addresses.length) {
              userProfile.addresses[0].street = 'test123';
              userProfile.addresses[0].city = 'test123';
              userProfile.addresses[0].zip = 'test123';
            }

            if (userProfile.phones && userProfile.phones.length) {
              userProfile.phones[0].number = '1234567890';
            }

            if (reg && reg.member && !reg.selfRegistration) {
              userProfile.membershipType = userProfile.options.membershipTypes[0];
              userProfile.membershipProduct = userProfile.options.membershipTypes[0].membershipPackages[0];
              userProfile.membershipPriceList = userProfile.options.membershipPriceLists[0];
            }

            userProfile.leadSource = userProfile.options.leadSources[0];
            userProfile.termsOfService = true;

            $scope.$apply();
          }
        }
      },

      /**
       * Binds test billing data generator on pressing Alt + T to prevent wasting time on typing.
       */
      bindTestBillingGenerator: function($scope, userProfile, reg) {
        var userProfileService = this;

        document.addEventListener('keydown', fillTestBillingData);
        $scope.$on('$destroy', function() {
          document.removeEventListener('keydown', fillTestBillingData);
        });

        function fillTestBillingData(event) {
          // Alt + T
          if (event.altKey && event.keyCode === 84) {

            userProfile.creditCards.forEach(cc => {
              if (userProfileService.creditCard.isNew(cc)) {
                cc.number = '4111111111111111';
                cc.ccv = '123';
                cc.expiryMonth = optsCCExpiryMonth[5];
                cc.expiryYear = optsCCExpiryYear[5];
              }
            });

            userProfile.eChecks.forEach(ec => {
              if (userProfileService.eCheck.isNew(ec)) {
                ec.bankName = 'Test Bank';
                ec.bankRoutingNumber = '021000021';
                ec.bankAccountNumber = '9876543210';
              }
            });

            $scope.$apply();
          }
        }
      },

      /**
       * Registers new user.
       * 1. Creates/updates main user profile part
       * 2. Creates/updates child entities: photo, addresses, phones, printer codes, documents
       */
      registerNewUser: function(centerId, userProfile, registration, fields) {
        const userProfileService = this;

        return userProfileService.save(centerId, userProfile, registration)
          .catch(err => dwAlerts.error(err, 'Failed to create user profile.'))
          .then(() => {
            const promises = [];

            // Photo
            promises.push(
              userProfileService.photo.save(centerId, userProfile)
                .catch(err => dwAlerts.error(err, 'Failed to upload photo.'))
            );

            // Addresses
            userProfile.addresses.forEach(addr => {
              promises.push(
                userProfileService.address.save(centerId, userProfile.id, addr)
                  .catch(err => dwAlerts.error(err, 'Failed to create address.'))
              );
            });

            // Phones
            userProfile.phones.forEach(phone => {
              promises.push(
                userProfileService.phone.save(centerId, userProfile.id, phone)
                  .catch(err => dwAlerts.error(err, 'Failed to create phone.'))
              );
            });

            // Printer Codes
            userProfile.printerCodes.forEach(prCode => {
              promises.push(
                userProfileService.printerCode.save(centerId, userProfile.id, prCode)
                  .catch(err => dwAlerts.error(err, 'Failed to create printer code.'))
              );
            });

            // Documents
            userProfile.documents.forEach(doc => {
              promises.push(
                userProfileService.document.save(centerId, userProfile.id, doc)
                  .catch(err => dwAlerts.error(err, 'Failed to create document.'))
              );
            });

            // Access Cards
            userProfile.accessCards.forEach(card => {
              card.userId = userProfile.id;
              promises.push(
                systemService.createAccessCard(centerId, card)
                  .catch(err => dwAlerts.error(err, 'Failed to create access card.'))
              );
            });

            if (fields) {
              promises.push(
                UserProfileFields.save(centerId, userProfile.id, fields)
                  .catch(err => dwAlerts.error(err, 'Failed to save custom fields.'))
              );
            }

            // Message board settings
            if (userProfile.saveMessageBoardUserSettings) {
              promises.push(
                userProfile.saveMessageBoardUserSettings()
                  .catch(err => dwAlerts.error(err, 'Failed to save message board user settings.'))
              );
            }

            return $q.all(promises);
          });
      },

      /**
       * Saves Billing Profile. Each credit card, handles default payment method, returns promise.
       */
      saveBillingProfile: function(centerId, user) {
        const userProfileService = this;
        const promises = [];

        user.creditCards.forEach(cc => {
          // Process only new credit cards
          if (userProfileService.creditCard.isNew(cc)) {
            promises.push(userProfileService.creditCard.save(centerId, user.id, cc)
              .then(result => {
                return $timeout(() => { // End cc ffBlock editing
                  const block = document.querySelector('#upCreditCard-' + result.id);
                  if (block) angular.element(block).scope().$broadcast('ffBlock::EditEnd');
                });
              })
              .catch(err => dwAlerts.error(err, 'Failed to create credit card.'))
            );
          }
        });

        angular.forEach(user.eChecks, eCheck => {
          // Process only new eCheck
          if (userProfileService.eCheck.isNew(eCheck)) {
            promises.push(userProfileService.eCheck.save(centerId, user.id, eCheck)
              .then(result => {
                return $timeout(() => { // End eCheck ffBlock editing
                  const block = document.querySelector('#upECheck-' + result.id);
                  if (block) angular.element(block).scope().$broadcast('ffBlock::EditEnd');
                });
              })
              .catch(err => dwAlerts.error(err, 'Failed to create eCheck.'))
            );
          }
        });

        // All credit cards saved
        return $q.all(promises)
          .then(results => {
            if (!user.billingMethod || !user.paymentMethodId && (user.billingMethod === 'cc' || user.billingMethod === 'ach')) {
              if (user.eChecks.length) {
                user.billingMethod = 'ach';
                user.paymentMethodId = user.eChecks[0].id;
              } else {
                if (user.creditCards.length) {
                  user.billingMethod = 'cc';
                  user.paymentMethodId = user.creditCards[0].id;
                } else {
                  console.warn('userProfileService.saveBillingProfile: user has no payment methods');
                }
              }
            }

            return userProfileService.saveBillingMethod(centerId, user)
              .then(() => results);
          });
      },

      saveBillingMethod: function(centerId, user) {
        const userProfileService = this;
        const data = { billingMethod: user.billingMethod, paymentMethodId: user.paymentMethodId };
        return userProfileService.updateRaw(centerId, user, data)
          .catch(err => dwAlerts.error(err, 'Failed to save billing method.'));
      },
    };
  });
