angular.module('deskworks.membership.service', [
  'ngResource',
  'deskworks.config'
])

  .factory('membershipService', function($resource, deskworksConfig) { 'use strict';
    var resType = $resource(deskworksConfig.getApiAbsUrl('/centers/:centerId/membership-types'));

    return {
      query: function(centerId) {
        return resType.query({centerId: centerId}).$promise;
      }
    };
  });
