export default angular.module('deskworks.dashboard.service', [
  'ngResource',
  'deskworks.config',
  'deskworks.helper',
  'datesDeserializer'
])

  .service('dashboardService', function($resource, deskworksConfig, helper, datesDeserializer) {
    const dashboardService = this;

    const desDates = datesDeserializer([
      'reservationsToday.startTime', 'reservationsToday.endTime', 'reservationsToday.updatedAt',
      'reservationsTomorrow.startTime', 'reservationsTomorrow.endTime', 'reservationsTomorrow.updatedAt',
      'reservationsConflicts.startTime', 'reservationsConflicts.endTime'
    ]);

    const resCenterStats = $resource(deskworksConfig.getApiAbsUrl('/dashboard/center-stats'), {}, {
      get: { method: 'GET', isArray: false, transformResponse: [desDates, deserialize] },
      update: { method: 'PUT' }
    });

    const resReservationConflicts = $resource(deskworksConfig.getApiAbsUrl('/dashboard/reservations-conflicts/:conflictId'), {}, {
      update: { method: 'PUT' }
    });

    /**
     * Center Stats for admin/coordinator
     */
    dashboardService.getCenterStats = (centerId) => resCenterStats.get({ centerId }).$promise,

    /**
     * Reset Reservation Conflict
     */
    dashboardService.resetReservationConflict = (conflictId) => resReservationConflicts.delete({ conflictId }).$promise;

    function deserialize(data) {
      data = angular.fromJson(data);

      data.selfRegistrations.forEach(user => {
        user.fullNameCompany = helper.join([user.company, user.fullName], ', ');
      });

      return data;
    }
  });
